<template>
  <div class="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submited">
      <label for>博客标题</label>
      <input type="text" v-model="blog.title" required>

      <label for>博客内容</label>
      <textarea name cols="30" rows="10" v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">

        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">

        <label>Angule.js</label>
        <input type="checkbox" value="Angule.js" v-model="blog.categories">

        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="(author ,index) in authors" :key="index">{{author}}</option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>
    <div v-if="submited">
      <h4>您的博客添加成功</h4>
    </div>
    <hr>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="(category,index) in blog.categories" :key="index">{{category}}</li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog: {
        title: '',
        content: '',
        categories: [],
        author: ''
      },
      authors: ['Herry', 'hemmi', 'okky'],
      submited: false
    }
  },
  methods: {
    post () {
      this.$http
        .post('https://vue-blog-c2af7.firebaseio.com/post.json', this.blog)
        .then(data => {
          console.log(data)
          this.submited = true
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.add-blog {
  box-sizing: border-box;
}
label {
  display: block;
  padding: 20px 0;
  text-align: left;
  font-weight: 400;
}
select,
label {
  width: 100%;
}
#checkboxes > input {
  display: inline-block;
}
#checkboxes > label {
  display: inline-block;
}
</style>



